'use client'

import { useEffect, useMemo, useState } from 'react'
import { HQSidebar } from '@/components/hq/HQSidebar'

/**
 * Slydes HQ — Analytics (Picture)
 *
 * Purpose: deeper performance with progressive disclosure.
 * Must stay aligned with MVP docs:
 * - Free vs Creator only (PAY-TIERS.md)
 * - Analytics locked on Free (MVP-MONETISATION.md prompt 2)
 * - Behaviour-based measurement: views, swipe depth, completion (PAY-TIERS.md, WHAT-WE-WILL-BUILD.md)
 * - Sharing = belief (MVP-MONETISATION.md)
 * - Hearts allowed as conversion signal (not social mechanic) (WHAT-WE-WILL-BUILD.md, what-we-will-never-build.md)
 */

export default function HQAnalyticsPage() {
  const [plan, setPlan] = useState<'free' | 'creator'>('creator')
  const [range, setRange] = useState<'7d' | '30d' | '90d'>('30d')

  useEffect(() => {
    try {
      const stored = window.localStorage.getItem('slydes_demo_plan')
      if (stored === 'free' || stored === 'creator') setPlan(stored)
    } catch {
      // ignore
    }
  }, [])

  useEffect(() => {
    try {
      window.localStorage.setItem('slydes_demo_plan', plan)
    } catch {
      // ignore
    }
  }, [plan])

  const isCreator = plan === 'creator'

  const slydes = useMemo(
    () => [
      { name: 'Camping', views: 12432, completion: 0.62, cta: 0.11, shares: 384, hearts: 2400, dropFrame: 'Frame 3 (What)' },
      { name: 'Just Drive', views: 6850, completion: 0.48, cta: 0.07, shares: 190, hearts: 980, dropFrame: 'Frame 2 (How)' },
    ],
    []
  )

  return (
    <div className="min-h-screen bg-gray-50 text-gray-900 dark:bg-[#1c1c1e] dark:text-white overflow-hidden">
      <div className="flex h-screen">
        {/* Sidebar */}
        <HQSidebar 
          activePage="analytics" 
          plan={plan} 
          onPlanChange={setPlan}
        />

        {/* Main */}
        <main className="flex-1 flex flex-col overflow-hidden">
          <header className="h-16 border-b border-gray-200 flex items-center justify-between px-8 shrink-0 bg-white/80 backdrop-blur-xl dark:border-white/10 dark:bg-[#1c1c1e]/80">
            <div>
              <h1 className="text-xl font-bold font-display tracking-tight text-gray-900 dark:text-white">Analytics</h1>
              <p className="text-sm text-gray-500 dark:text-white/60">Behaviour, not vanity • drop-off • actions</p>
            </div>
            <div className="flex items-center gap-2">
              <div className="flex items-center gap-1 rounded-xl bg-gray-100 p-1 dark:bg-white/10">
                {(['7d', '30d', '90d'] as const).map((r) => (
                  <button
                    key={r}
                    onClick={() => setRange(r)}
                    className={`px-2.5 py-1 rounded-lg text-xs font-semibold transition-colors ${
                      range === r ? 'bg-white text-gray-900 shadow-sm' : 'text-gray-600 hover:text-gray-900'
                    } dark:${range === r ? 'bg-[#2c2c2e] text-white' : 'text-white/60 hover:text-white'}`}
                  >
                    {r.toUpperCase()}
                  </button>
                ))}
              </div>
              <button className="px-4 py-2 bg-gray-100 text-gray-800 font-semibold text-sm rounded-xl hover:bg-gray-200 transition-colors dark:bg-white/10 dark:text-white dark:hover:bg-white/15">
                Compare
              </button>
            </div>
          </header>

          <div className="flex-1 overflow-y-auto p-8">
            <div className="max-w-6xl">
            <div className="relative">
              {/* Locked overlay (Free) */}
              {!isCreator && (
                <div className="absolute inset-0 z-20 flex items-start justify-center pt-16">
                  <div className="w-full max-w-xl rounded-3xl border border-gray-200 bg-white/95 backdrop-blur-xl shadow-xl p-6 dark:border-white/10 dark:bg-[#2c2c2e]/90">
                    <div className="text-xs uppercase tracking-wider font-semibold text-gray-500 dark:text-white/60">Creator feature</div>
                    <div className="mt-1 text-2xl font-bold">Unlock analytics</div>
                    <div className="mt-2 text-sm text-gray-600 dark:text-white/70">
                      See swipe depth, completion, and drop-off points — so you know what to fix and what to share more.
                    </div>
                    <div className="mt-5 flex items-center gap-2">
                      <button className="px-5 py-2.5 bg-gradient-to-r from-blue-600 to-cyan-500 text-white font-semibold rounded-xl hover:opacity-90 transition-opacity shadow-lg shadow-blue-500/15">
                        Upgrade to Creator (£25/mo)
                      </button>
                      <button className="px-4 py-2 bg-gray-100 text-gray-800 font-semibold text-sm rounded-xl hover:bg-gray-200 transition-colors dark:bg-white/10 dark:text-white dark:hover:bg-white/15">
                        Not now
                      </button>
                    </div>
                  </div>
                </div>
              )}

              {/* Content (blurred on Free) */}
              <div className={`${!isCreator ? 'blur-[8px] opacity-60 pointer-events-none select-none' : ''}`}>
                {/* Overview */}
                <div className="grid grid-cols-12 gap-4 mb-8">
                  {[
                    { label: 'Views', value: '19.2k', sub: `Last ${range.toUpperCase()}` },
                    { label: 'Completion', value: '56%', sub: 'Reached final frame' },
                    { label: 'CTA clicks', value: '9.6%', sub: 'Clicks / viewers' },
                    { label: 'Shares', value: '574', sub: 'Sharing = belief' },
                    { label: 'Hearts', value: '3.4k', sub: 'Interest signal' },
                  ].map((s) => (
                    <div key={s.label} className="col-span-12 sm:col-span-6 lg:col-span-3 xl:col-span-2 p-5 rounded-2xl bg-white border border-gray-200 shadow-sm dark:bg-[#2c2c2e] dark:border-white/10">
                      <div className="text-xs text-gray-500 dark:text-white/60 uppercase tracking-wider font-semibold mb-1">{s.label}</div>
                      <div className="text-2xl font-mono font-bold mb-1">{s.value}</div>
                      <div className="text-sm text-gray-500 dark:text-white/60">{s.sub}</div>
                    </div>
                  ))}
                </div>

                {/* Breakdown + Drop-off */}
                <div className="grid grid-cols-12 gap-6 mb-8">
                  {/* Per-Slyde */}
                  <div className="col-span-12 lg:col-span-7 bg-white rounded-3xl border border-gray-200 shadow-sm overflow-hidden dark:bg-[#2c2c2e] dark:border-white/10">
                    <div className="p-6 border-b border-gray-200 dark:border-white/10">
                      <div className="flex items-center justify-between gap-4">
                        <div>
                          <div className="text-xs uppercase tracking-wider font-semibold text-gray-500 dark:text-white/60">Per Slyde</div>
                          <div className="mt-1 flex items-center gap-2">
                            <svg className="w-4 h-4 text-blue-600 dark:text-cyan-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12l2 2 4-4M12 22a10 10 0 100-20 10 10 0 000 20z" />
                            </svg>
                            <div className="text-lg font-display font-bold">What’s working</div>
                          </div>
                        </div>
                        <div className="text-xs px-2 py-1 rounded-full bg-gray-100 text-gray-700 dark:bg-white/10 dark:text-white/70">Sorted by shares</div>
                      </div>
                    </div>
                    <div className="p-6 space-y-4">
                      {slydes
                        .slice()
                        .sort((a, b) => b.shares - a.shares)
                        .map((s) => (
                          <div key={s.name} className="p-4 rounded-2xl bg-gray-50 border border-gray-200 dark:bg-white/5 dark:border-white/10">
                            <div className="flex items-start justify-between gap-3">
                              <div>
                                <div className="text-base font-display font-bold">{s.name}</div>
                                <div className="text-sm text-gray-600 dark:text-white/60 mt-1">Biggest drop: {s.dropFrame}</div>
                              </div>
                              <div className="text-right">
                                <div className="text-sm font-semibold">{s.shares} shares</div>
                                <div className="text-xs text-gray-500 dark:text-white/50">{s.views.toLocaleString()} views</div>
                              </div>
                            </div>
                            <div className="mt-3 grid grid-cols-3 gap-3 text-sm">
                              <div className="p-3 rounded-xl bg-white border border-gray-200 dark:bg-[#2c2c2e] dark:border-white/10">
                                <div className="text-xs text-gray-500 dark:text-white/60 uppercase tracking-wider font-semibold">Completion</div>
                                <div className="mt-1 font-mono font-bold">{Math.round(s.completion * 100)}%</div>
                              </div>
                              <div className="p-3 rounded-xl bg-white border border-gray-200 dark:bg-[#2c2c2e] dark:border-white/10">
                                <div className="text-xs text-gray-500 dark:text-white/60 uppercase tracking-wider font-semibold">CTA</div>
                                <div className="mt-1 font-mono font-bold">{Math.round(s.cta * 1000) / 10}%</div>
                              </div>
                              <div className="p-3 rounded-xl bg-white border border-gray-200 dark:bg-[#2c2c2e] dark:border-white/10">
                                <div className="text-xs text-gray-500 dark:text-white/60 uppercase tracking-wider font-semibold">Hearts</div>
                                <div className="mt-1 font-mono font-bold">{(s.hearts / 1000).toFixed(1)}k</div>
                              </div>
                            </div>
                          </div>
                        ))}
                    </div>
                  </div>

                  {/* Drop-off focus */}
                  <div className="col-span-12 lg:col-span-5 bg-white rounded-3xl border border-gray-200 shadow-sm overflow-hidden dark:bg-[#2c2c2e] dark:border-white/10">
                    <div className="p-6 border-b border-gray-200 dark:border-white/10">
                      <div className="text-xs uppercase tracking-wider font-semibold text-gray-500 dark:text-white/60">Drop-off</div>
                      <div className="mt-1 flex items-center gap-2">
                        <svg className="w-4 h-4 text-blue-600 dark:text-cyan-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M11.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.539 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.783.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.719c-.783-.57-.38-1.81.588-1.81h3.462a1 1 0 00.95-.69l1.07-3.292z" />
                        </svg>
                        <div className="text-lg font-display font-bold">The one chart that matters</div>
                      </div>
                      <div className="text-sm text-gray-600 dark:text-white/60 mt-1">Reach % by frame (Camping)</div>
                    </div>
                    <div className="p-6 space-y-3">
                      {[
                        { label: 'Frame 1 (Hook)', pct: 1.0 },
                        { label: 'Frame 2 (How)', pct: 0.78 },
                        { label: 'Frame 3 (What)', pct: 0.55 },
                        { label: 'Frame 4 (Trust)', pct: 0.49 },
                        { label: 'Frame 5 (Proof)', pct: 0.45 },
                        { label: 'Frame 6 (Action)', pct: 0.40 },
                      ].map((r) => (
                        <div key={r.label}>
                          <div className="flex items-center justify-between text-sm mb-1">
                            <div className="text-gray-700 dark:text-white/80">{r.label}</div>
                            <div className="font-mono font-semibold">{Math.round(r.pct * 100)}%</div>
                          </div>
                          <div className="h-2 rounded-full bg-gray-200 overflow-hidden dark:bg-white/10">
                            <div className="h-full rounded-full bg-gradient-to-r from-blue-600 to-cyan-500" style={{ width: `${r.pct * 100}%` }} />
                          </div>
                        </div>
                      ))}

                      <div className="mt-5 relative overflow-hidden rounded-2xl bg-white border border-gray-200 shadow-sm dark:bg-[#2c2c2e] dark:border-white/10">
                        <div className="absolute inset-x-0 top-0 h-1 bg-gradient-to-r from-blue-600 to-cyan-500" />
                        <div className="p-4">
                          <div className="flex items-start justify-between gap-4">
                            <div className="min-w-0">
                              <div className="flex items-center gap-2">
                                <span className="inline-flex items-center gap-1.5 px-2 py-0.5 rounded-md bg-blue-50 text-blue-700 text-xs font-semibold uppercase tracking-wider dark:bg-blue-500/10 dark:text-cyan-300">
                                  <span className="w-1.5 h-1.5 rounded-full bg-blue-500 dark:bg-cyan-400" />
                                  Insight
                                </span>
                              </div>
                              <div className="mt-2 text-sm text-gray-700 dark:text-white/70">
                                Biggest drop is Frame 3 (What). Shorten the copy, lead with the payoff, and keep the video moving.
                              </div>
                            </div>
                            <a
                              href="/demo/editor-mockup?slyde=camping"
                              className="shrink-0 inline-flex items-center gap-2 px-3 py-2 rounded-xl bg-gradient-to-r from-blue-600 to-cyan-500 text-white text-sm font-semibold hover:opacity-90 transition-opacity shadow-sm shadow-blue-500/15"
                            >
                              Open
                              <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 7l5 5m0 0l-5 5m5-5H6" />
                              </svg>
                            </a>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>

                {/* Footer note */}
                <div className="text-xs text-gray-500 dark:text-white/50">
                  Note: This is a visual mock ("picture"). Real analytics reads aggregated events (frame views, shares, CTA clicks) as defined in `SLYDESBUILD.md`.
                </div>
              </div>
            </div>
            </div>
          </div>
        </main>
      </div>

      {/* Ambient glow */}
      <div className="fixed top-0 left-1/4 w-[600px] h-[600px] bg-blue-500/10 rounded-full blur-[180px] pointer-events-none dark:bg-blue-500/12" />
      <div className="fixed bottom-0 right-1/4 w-[520px] h-[520px] bg-cyan-500/10 rounded-full blur-[180px] pointer-events-none dark:bg-cyan-500/12" />
    </div>
  )
}


