'use client'

import { useEffect, useState } from 'react'
import { ChevronLeft, ChevronRight, LayoutDashboard, Smartphone, BarChart3, Inbox, Palette, Settings } from 'lucide-react'

interface HQSidebarProps {
  activePage: 'dashboard' | 'slydes' | 'analytics' | 'inbox' | 'brand' | 'settings'
  plan: 'free' | 'creator'
  onPlanChange: (plan: 'free' | 'creator') => void
  slydeCount?: number
  inboxCount?: number
}

export function HQSidebar({ activePage, plan, onPlanChange, slydeCount = 2, inboxCount = 3 }: HQSidebarProps) {
  const [collapsed, setCollapsed] = useState(false)
  const isCreator = plan === 'creator'

  // Persist collapsed state
  useEffect(() => {
    try {
      const stored = window.localStorage.getItem('slydes_sidebar_collapsed')
      if (stored === 'true') setCollapsed(true)
    } catch {
      // ignore
    }
  }, [])

  useEffect(() => {
    try {
      window.localStorage.setItem('slydes_sidebar_collapsed', String(collapsed))
    } catch {
      // ignore
    }
  }, [collapsed])

  const navItems = [
    { id: 'dashboard', label: 'Dashboard', href: '/demo/hq-dashboard', icon: LayoutDashboard },
    { id: 'slydes', label: 'Slydes', href: '/demo/hq-mockup', icon: Smartphone, badge: slydeCount },
    { id: 'analytics', label: 'Analytics', href: '/demo/hq-analytics', icon: BarChart3, locked: !isCreator },
    { id: 'inbox', label: 'Inbox', href: '/demo/hq-inbox', icon: Inbox, badge: inboxCount },
  ]

  const bottomNavItems = [
    { id: 'brand', label: 'Brand', href: '/demo/hq-brand', icon: Palette },
    { id: 'settings', label: 'Settings', href: '/demo/hq-settings', icon: Settings },
  ]

  return (
    <aside className={`${collapsed ? 'w-[72px]' : 'w-72'} bg-white border-r border-gray-200 flex flex-col dark:bg-[#2c2c2e] dark:border-white/10 transition-all duration-300 shrink-0`}>
      
      {/* Logo */}
      <div className="p-4 border-b border-gray-200 dark:border-white/10">
        <div className="flex items-center gap-3">
          <div className="w-9 h-9 bg-gradient-to-br from-blue-600 to-cyan-500 rounded-xl flex items-center justify-center shadow-sm shrink-0">
            <svg className="w-5 h-5 text-white" viewBox="0 0 24 24" fill="currentColor">
              <path d="M4 4h6v6H4V4zm10 0h6v6h-6V4zM4 14h6v6H4v-6zm10 0h6v6h-6v-6z"/>
            </svg>
          </div>
          {!collapsed && (
            <div className="min-w-0">
              <div className="text-lg font-bold tracking-tight text-gray-900 dark:text-white">Slydes</div>
              <div className="text-xs text-gray-500 dark:text-white/50 -mt-0.5">HQ</div>
            </div>
          )}
        </div>
      </div>

      {/* Navigation */}
      <nav className="flex-1 p-3 space-y-1">
        {navItems.map((item) => {
          const isActive = activePage === item.id
          const Icon = item.icon
          
          return (
            <a
              key={item.id}
              href={item.href}
              className={`flex items-center gap-3 px-3 py-2.5 rounded-xl transition-colors cursor-pointer relative ${
                isActive 
                  ? 'bg-gray-100 text-gray-900 dark:bg-white/10 dark:text-white' 
                  : 'text-gray-600 hover:text-gray-900 hover:bg-gray-100 dark:text-white/60 dark:hover:text-white dark:hover:bg-white/10'
              }`}
              title={collapsed ? item.label : undefined}
            >
              {isActive && (
                <div className="absolute left-0 top-1/2 -translate-y-1/2 w-1 h-6 bg-gradient-to-b from-blue-600 to-cyan-500 rounded-r-full" />
              )}
              <Icon className="w-5 h-5 shrink-0" />
              {!collapsed && (
                <>
                  <span className="text-sm font-medium">{item.label}</span>
                  {item.badge && (
                    <span className="ml-auto text-xs bg-gray-200 text-gray-700 px-2 py-0.5 rounded-full dark:bg-white/20 dark:text-white/80">
                      {item.badge}
                    </span>
                  )}
                  {item.locked && (
                    <span className="ml-auto text-[11px] bg-gray-200 text-gray-700 px-2 py-0.5 rounded-full dark:bg-white/20 dark:text-white/80">
                      Locked
                    </span>
                  )}
                </>
              )}
            </a>
          )
        })}

        {/* Divider */}
        <div className="my-3 border-t border-gray-200 dark:border-white/10" />

        {/* Bottom nav items */}
        {bottomNavItems.map((item) => {
          const isActive = activePage === item.id
          const Icon = item.icon
          
          return (
            <a
              key={item.id}
              href={item.href}
              className={`flex items-center gap-3 px-3 py-2.5 rounded-xl transition-colors cursor-pointer relative ${
                isActive 
                  ? 'bg-gray-100 text-gray-900 dark:bg-white/10 dark:text-white' 
                  : 'text-gray-600 hover:text-gray-900 hover:bg-gray-100 dark:text-white/60 dark:hover:text-white dark:hover:bg-white/10'
              }`}
              title={collapsed ? item.label : undefined}
            >
              {isActive && (
                <div className="absolute left-0 top-1/2 -translate-y-1/2 w-1 h-6 bg-gradient-to-b from-blue-600 to-cyan-500 rounded-r-full" />
              )}
              <Icon className="w-5 h-5 shrink-0" />
              {!collapsed && <span className="text-sm font-medium">{item.label}</span>}
            </a>
          )
        })}
      </nav>

      {/* Plan Card */}
      {!collapsed && (
        <div className="p-3">
          <div className="p-4 rounded-2xl bg-gradient-to-br from-blue-50 via-white to-cyan-50 border border-gray-200 shadow-sm dark:from-blue-600/12 dark:via-white/5 dark:to-cyan-600/12 dark:border-white/10">
            <div className="text-xs text-blue-700 font-semibold uppercase tracking-wider mb-1 dark:text-blue-300">Plan</div>
            <div className="text-base font-display font-bold text-gray-900 dark:text-white">{isCreator ? 'Creator' : 'Free'}</div>
            <div className="mt-1 text-xs text-gray-600 dark:text-white/60">
              {isCreator ? '10 Slydes • Analytics • No watermark' : '1 Slyde • Watermark'}
            </div>
            
            {/* Demo toggle */}
            <div className="mt-3 flex items-center gap-1 rounded-xl bg-gray-100 p-1 dark:bg-white/10">
              <button
                onClick={() => onPlanChange('free')}
                className={`flex-1 px-2.5 py-1 rounded-lg text-xs font-semibold transition-colors ${
                  !isCreator ? 'bg-white text-gray-900 shadow-sm dark:bg-[#3a3a3c] dark:text-white' : 'text-gray-600 hover:text-gray-900 dark:text-white/60 dark:hover:text-white'
                }`}
              >
                Free
              </button>
              <button
                onClick={() => onPlanChange('creator')}
                className={`flex-1 px-2.5 py-1 rounded-lg text-xs font-semibold transition-colors ${
                  isCreator ? 'bg-white text-gray-900 shadow-sm dark:bg-[#3a3a3c] dark:text-white' : 'text-gray-600 hover:text-gray-900 dark:text-white/60 dark:hover:text-white'
                }`}
              >
                Creator
              </button>
            </div>

            {!isCreator && (
              <a
                href="/demo/hq-settings"
                className="mt-3 block w-full px-4 py-2 bg-gradient-to-r from-blue-600 to-cyan-500 text-white font-semibold text-sm rounded-xl text-center hover:opacity-90 transition-opacity shadow-lg shadow-blue-500/15"
              >
                Upgrade to Creator
              </a>
            )}
          </div>
        </div>
      )}

      {/* Collapse Toggle */}
      <div className="p-3 border-t border-gray-200 dark:border-white/10">
        <button
          onClick={() => setCollapsed(!collapsed)}
          className="flex items-center justify-center w-full gap-2 px-3 py-2 rounded-xl text-gray-600 hover:text-gray-900 hover:bg-gray-100 transition-colors dark:text-white/60 dark:hover:text-white dark:hover:bg-white/10"
        >
          {collapsed ? (
            <ChevronRight className="w-5 h-5" />
          ) : (
            <>
              <ChevronLeft className="w-5 h-5" />
              <span className="text-sm font-medium">Collapse</span>
            </>
          )}
        </button>
      </div>

      {/* User Profile */}
      <div className="p-3 border-t border-gray-200 dark:border-white/10">
        <div className={`flex items-center gap-3 p-2 rounded-xl hover:bg-gray-100 transition-colors cursor-pointer dark:hover:bg-white/10 ${collapsed ? 'justify-center' : ''}`}>
          <div className="w-9 h-9 rounded-full bg-gradient-to-br from-emerald-500 to-teal-600 flex items-center justify-center text-white font-bold text-sm shrink-0">
            W
          </div>
          {!collapsed && (
            <div className="flex-1 min-w-0">
              <div className="text-sm font-medium text-gray-900 truncate dark:text-white">WildTrax</div>
              <div className="text-xs text-gray-500 truncate dark:text-white/50">wildtrax.slydes.io</div>
            </div>
          )}
        </div>
      </div>
    </aside>
  )
}

